import React, {useState} from 'react';
import './App.css';
import { Navbar, Container, Nav, NavDropdown, Button } from 'react-bootstrap';
import Data from './data.js'
import Detail from './Detail.js'
import axios from 'axios';
import { Link, Route, Switch } from 'react-router-dom'

function App() {
  let [shoes, _shoes] = useState(Data);
  let [loading, _loading] = useState(false);
  let [stock, _stock] = useState([10, 11, 12]);
  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">MS Shopping Mall</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to='/'>Home</Nav.Link>
              <Nav.Link as={Link} to='/detail'>Detail</Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
<Switch>
    <Route exact path="/">
      <div className='Jumbotron'>
        <h1>20% Season OFF!</h1>
        <p> This is a simple hero unit, a simple jumbotron-style component for calling
          extra attention to featured content or information </p>
        <p>
          <Button variant="primary">Learn more</Button>
        </p>
      </div>
      <div className="container">
      <div className="row">
        {
          shoes.map((a, i)=> {
            return <Card shoes={shoes[i]} i={i} key={i}/>
          })
        }
      </div>
      {
        loading == true
        ? <p> 로딩중입니다</p>
        : null
      }
      <button className="btn btn-primary" onClick={()=>{
//axios를 통한 Ajax 사용 : GET 요청
//.then: 데이터 불러오기 성공 시 실행 함수
//.catch: 데이터 불러오기 실패 시 실행 함수
//서버와 주고받는 자료형: JSON (Object형태로 주고받을 수 없다)
//fetch() 보다 axios.get()이 더 호환성 좋음
//axios.post('server URL', (id:dd, pw:dd)) ajax로 데이터 송신
        _loading(true)
        axios.get('https://codingapple1.github.io/shop/data2.json')
        .then((result)=>{
          _shoes([...shoes,...result.data])
        _loading(false)
        })
        .catch(()=>{
          console.log('실패')
        _loading(false)
        })
      }}>더보기</button>
       </div>
    </Route>
    <Route path="/detail/:id">
        <Detail shoes={shoes} stock={stock} _stock={_stock}/>
    </Route>
    <Route path='/:id'>
    {/* :id: 모든 문자열 */}
        <div>아무거나</div>
    </Route>
  </Switch>
    </div>
  );
}
function Card(props){
  return(
    <div className='col-md-4'>
      <img src={'https://codingapple1.github.io/shop/shoes'+(props.i+1)+'.jpg'} width='100%' />
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content} / {props.shoes.price}</p>
    </div>
  )
} export default App;
