import React, {useEffect, useState} from 'react';
import { useHistory, useParams } from 'react-router-dom'
import styled from 'styled-components';
import './Detail.css'

let Box = styled.div`
    padding : 20px;
    color: red;
`;
let Title = styled.h4`
    font-size : 25px;
    color : ${ props => props.색상 }
`;
function Detail(props){
    let [alert, _alert] = useState(true);
//useEffect(()=>{},[State]) -> 'State'가 없데이트 될 떄만 실행해주세요~
//setTimeout(function(){}, ##ms); -> ##ms후에 실행
//let timer = setTimeout(function(){}) 와 같은 형태로 변수에 저장해서 자주 사용함
//setTimeout() 쓸 때 꼭 return에 clearTimeout 콜백함수로 선언해 주어야 다른 문제 안 생김

    let [inputData, _inputData] = useState();
    useEffect(()=>{
        let timer= setTimeout(()=>{_alert(false) }, 2000);
        return ()=>{ clearTimeout(timer)}
    },[]);

    //useParams: object 형태
    let { id } = useParams();
    let product = props.shoes.find(function(a){
        return a.id == id
    });
    let history= useHistory();

    return (
      <div className="container">
          <Box>
              <Title className="red"> Detail </Title>
          </Box>
         {/* e.target.value   :  현재 입력된 데이터 */}
          <input onChange={ (e)=> {_inputData(e.target.value)}}/>

          { inputData }
        {
          alert == true
          ?<div className="my-alert2">
              <p>재고가 얼마 남지 않았습니다!</p>
          </div>
            : null
        }
      <div className="row">
        <div className="col-md-6">
          <img src={"https://codingapple1.github.io/shop/shoes"+(+id+1)+".jpg"} width="100%" />
        </div>
        <div className="col-md-6 mt-4">
            <h4 className="pt-5">{product.title}</h4>
            <p>{product.content}</p>
            <p>{product.price} 원</p>

            <Info stock={props.stock} id={id}/>

            <button className="btn btn-danger" onClick={ ()=> { props._stock([9, 10, 11])}}> 주문하기 </button> 
            <button className="btn btn-danger" onClick={()=>{
            history.goBack();
            //history.push('/');
          }}>뒤로가기</button> 
        </div>
      </div>
    </div>
    )
  }

  function Info(props){
      return(
          <p>재고: {props.stock[props.id]} </p>
      )
  }

  export default Detail;
  